@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap&subset=cyrillic-ext);
*{padding:0;margin:0;border:0}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html,body{width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}body{font-size:14px;font-family:"Roboto", sans-serif}body._lock{overflow:hidden}.wrapper{width:100%;min-height:100%;overflow:hidden;display:flex;flex-direction:column}.app-wrapper{max-width:1402px;margin:0 auto;padding:20px 20px;min-width:550px}.menu{display:flex;position:relative;margin-bottom:30px}.menu__input{width:250px;height:35px;border:1px solid gray;border-radius:4px;padding-left:10px;font-size:16px}.menu button{margin-left:10px;width:120px;border-radius:4px;font-weight:500;background-color:cadetblue}.menu button:disabled{cursor:default;background-color:#bdbdbd}.menu__error-message{position:absolute;color:red;top:42px;left:10px}

.img_block{width:215px;margin:10px 0 0 10px;border-radius:4px;border:1px solid gray;overflow:hidden}.img_block img{height:auto;width:213px}

.group{border:1px solid gray;margin-bottom:30px;border-radius:4px;padding:0 10px 10px 0}.group__title{padding:10px 0 0 10px;font-size:16px;font-weight:500}.group__images{display:flex;flex-wrap:wrap}

